<template>
	<div class="address-index container">
		<Header></Header>
		<section>
			<ul>
				<li 
					@click="goList(item)"
					v-for='(item,index) in list'
					:key="index">
					<div>
						<span>{{item.name}}</span>
						<span>{{item.tel}}</span>
					</div>
					<div class='city'>
						<span class='active' v-if='item.isDefault == 1'>[默认]</span>
						<span>{{item.province}}</span>
						<span>{{item.city}}</span>
						<span>{{item.county}}</span>
						<span>{{item.addressDetail}}</span>
					</div>
				</li>
			</ul>
			<div class="add-path" @click='goList("add")'>添加地址</div>
		</section>
		<Tabbar></Tabbar>
	</div>
</template>

<script>
	import Header from '@/components/address/Header.vue'
	import Tabbar from '@/components/common/Tabbar.vue'
	import http from '@/common/api/request.js'
	import {mapState,mapMutations} from 'vuex'
	import bus from '@/common/bus.js'
	export default{
	data () {
			return {
				pathStatus:false
			}
		},
		components:{
			Header,
			Tabbar
		},
		created(){
			// 从订单页面进来的
			if( this.$route.query.type == 'select' ){
				this.pathStatus = true;
			}
			this.getData()
		},
		computed:{
			...mapState({
				list:state=>state.address.list
			})
		},
		methods:{
			...mapMutations(['initData']),
			getData(){
				http.$axios({
					url:'/api/selectAddress',
					method:'post',
					headers:{
						token:true
					}
				}).then(res=>{
					this.initData(res.data)
				})
			},
			goList(option){
				// console.log(option)
				 //this.pathStatus为true代表从订单页面进入的：选择状态
				if( this.pathStatus ){
					bus.$emit('selectPath', JSON.stringify(option) );
					this.$router.back();
					return;
				}
				this.$router.push({
					name:'address-list',
					params:{
						key:JSON.stringify(option)
					}
				})
			}
		}
	}
</script>

<style scoped>
	section{
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: #F7F7F7;
	}
	section ul{
		width: 100%;
	}
	section ul li{
		 padding:0.266666rem 0.4rem;
		margin:0.16rem 0;
		background-color: #FFFFFF;
	}
	section ul li span{
		 padding-right:0.4rem;
		 font-size:0.426666rem;
	}
	.add-path{
		 margin-top:0.8rem;
		width: 3.2rem;
		line-height: 1.066666rem;
		font-size:0.48rem;
		text-align: center;
		color:#FFFFFF;
		background-color:darkgreen;
		border-radius: 6px;
	}
	.active{
	     color:#b0352f;
	 }
	 .city span{
		 padding-right:0.16rem;
	 }
	::v-deep .tabbar{
	    border-top:2px solid #ccc;
	}
</style>